import React, { Component } from 'react'
import styles from './_ListItemTitle.scss';

export default class ListItemTitle extends Component {
  constructor(props) {
    super(props)
    this.state = {
      createMode: this.props.mode === 'new',
      showContent: this.props.showContent,
      editMode: this.props.showContent ? styles.editMode : '',
      isComplete: this.props.complete,
      isImportant: this.props.important,
      cbIcon: this.props.complete ? 'fa-check-square' : 'fa-square',
      starIcon: this.props.important ? 'fas' : 'far',
      titleText: this.props.text ? this.props.text : '',
      titlePlaceholder: this.props.text ? '' : 'Type Something Here…'
    }
    this.editClick = this.editClick.bind(this);
    this.toggleComplete = this.toggleComplete.bind(this);
    this.toggleImportant = this.toggleImportant.bind(this);
  }
  editClick(){
    const bool = !this.state.showContent;
    this.setState({
      showContent: bool,
      editMode: bool ? styles.editMode : '',
    }, () => {
      this.props.onEdit({
        showContent: bool
      })
    })
  }
  toggleComplete(){
    const isComplete = !this.state.isComplete
    this.setState({
      isComplete,
    })
  }
  toggleImportant(){
    this.props.onToggleComplete(this.props.important)
  }
  render () {
    const starStyle = this.props.important ? styles.important : ''
    const completeStyle = this.state.isComplete ? styles.complete : ''
    return (
      <div className={`${styles.ListItemTitle} ${completeStyle} ${starStyle}`}>
        <div className={styles.checkbox} onClick={this.toggleComplete}>
          <span className={`${this.state.isComplete ? '' : styles.hide }`}><i className={`fas fa-check-square fa-lg`} /></span>
          <span className={`${this.state.isComplete ? styles.hide : '' }`}><i className={`fas fa-square fa-lg`} /></span>
        </div>
        {
          this.state.createMode 
          ? <input type='text' placeholder={`${this.state.titlePlaceholder}`} />
          : <p className={styles.text}>{this.props.text}</p>
        }
        <div className={styles.action}>
          <div className={styles.importantMask} onClick={this.toggleImportant}>
            <span className={`${this.props.important ? '' : styles.hide }`}><i className={`fas fa-star fa-lg`} /></span>
            <span className={`${this.props.important ? styles.hide : '' }`}><i className={`far fa-star fa-lg`} /></span>
          </div>
          <div 
            className={`${styles.edit} ${this.state.editMode}`}
            onClick={this.editClick}>
            <i className="fas fa-pencil-alt fa-lg"></i>
          </div>
        </div>
      </div>
    )
  }
}


