import React, { Component } from 'react'
import PropTypes from 'prop-types';
import ListItemTitle from './ListItemTitle/ListItemTitle'
import ListItemSubtitle from './ListItemSubtitle/ListItemSubtitle'
import ListContent from './ListContent/ListContent'
import styles from './_ListItem.scss'

export default class ListItem extends Component {
  constructor(props) {
    super(props)
    let important = this.props.item.important,
      title = this.props.item.title,
      createMode =  this.props.mode === 'new',
      complete = this.props.item.complete
        
    this.state = {
      title,
      important,
      createMode,
      complete,
      showContent: this.props.item.showContent || createMode,
      isImportant: important ? styles.important : '',
      isShowSubTitle: this.title ? '' : styles.hide,
    }
    this.onCancel = this.onCancel.bind(this);
    this.onAddTask = this.onAddTask.bind(this);
    this.onEdit = this.onEdit.bind(this);
    this.onToggleComplete = this.onToggleComplete.bind(this);
  }
  onCancel(){
    if(this.props.onCancel) this.props.onCancel();
  }
  onAddTask(){
    if(this.props.onAddTask) this.props.onAddTask();
  }
  onEdit({showContent}){
    if(!showContent) this.onCancel()
    this.clickChild({showContent});
  }
  onToggleComplete(bool){
   bool = !bool
    this.setState({
      important: bool,
    })
  }
  render() {
    const importantStyle = this.state.important ? styles.important : ''
    return (
      <div className={`${styles.ListItem}`}>
        <div className={`${styles.header} ${importantStyle}`}>
          <ListItemTitle 
            mode={this.props.mode}
            text={this.state.title}
            important={this.state.important}
            editable={this.state.editable}
            complete={this.state.complete}
            showContent={this.state.showContent}
            onEdit={this.onEdit}
            onToggleComplete={this.onToggleComplete}/>
          <ListItemSubtitle 
            date={this.props.item.date}
            file={this.props.item.file}
            comment={this.props.item.comment}
            showContent={this.state.showContent}
          />
        </div>
        <ListContent 
          createMode={this.state.createMode}
          setClick={click => this.clickChild = click}
          showContent={this.state.showContent}
          onCancel={this.onCancel}
          onAddTask={this.onAddTask}
          item={this.props.item}/>
      </div>
    )
  }
}

ListItem.propTypes  = {
  item: PropTypes.object
}

ListItem.defaultProps = {
  item: {
    title: '',
    editable: false,
    complete: false,
    important: false,
    date: 0,
    comment: '',
    file: null,
    showContent: false
  }
}


