import React, { Component } from 'react'
import { isEmpty } from 'lodash';
import styles from './_ListItemSubtitle.scss'
import PropTypes from 'prop-types';
import * as moment from 'moment';

export default class ListItemSubtitle extends Component {
  constructor(props) {
    super(props)
    const file = this.props.file || {}
    const showSubTitle = !!(this.props.date || !isEmpty(this.props.file) || this.props.comment || !this.props.showContent)
    this.state = {
      subTitleClass: showSubTitle ? '' : styles.hide,
      showCalender: this.props.date ? styles.show : '',
      showFile: Object.keys(file).length ? styles.show : '',
      showComment: this.props.comment ? styles.show : ''
    }
  }
  render() {
    return (
      <div className={`${styles.ListItemSubtitle} ${this.state.subTitleClass}`}>
        <div className={`${styles.icon} ${this.state.showCalender}`}>
          <i className="far fa-calendar-alt"></i>
          <span className='text'>{moment(this.props.date).format('YYYY/MM/DD')}</span>
        </div>
        <div className={`${styles.icon} ${this.state.showFile}`}>
          <i className="far fa-file"></i>
        </div>
        <div className={`${styles.icon} ${this.state.showComment}`}>
          <i className="far fa-comment-dots"></i>
        </div>
      </div>
    )
  }
}

ListItemSubtitle.propTypes = {
  date: PropTypes.number,
  file: PropTypes.object,
  comment: PropTypes.string,
  showContent: PropTypes.bool
}

ListItemSubtitle.defaultProps = {
  date: 0,
  file: {},
  comment: '',
  showContent: false
}


