import React, { Component } from 'react'
import AdditionBtn from './AdditionBtn';
import ListItem from 'components/ListItem/ListItem';

export default class TaskAddition extends Component {
  constructor() {
    super()
    this.state = {
      showCreateForm: false
    }
    this.setCreateFormState = this.setCreateFormState.bind(this)
  }
  setCreateFormState(status){
    this.setState({
      showCreateForm: status  
    })
  }
  cancelTask(){
    this.setCreateFormState(false);
  }
  addTask(){
    this.setCreateFormState(false);
  }
  render() {
    return (
      <div>
        {
          this.state.showCreateForm
          ? <ListItem
            onCancel={this.cancelTask}
            onAddTask={this.addTask}
            setCreateFormState={this.setCreateFormState}
            mode="new"/>
          : <AdditionBtn setCreateFormState={this.setCreateFormState} />
        }
      </div>
    )
  }
}



