import React, { Component } from 'react'
import styles from './index.scss';

export default class AdditionBtn extends Component {
  constructor(props){
    super(props)
    this.showCreateForm = this.showCreateForm.bind(this)
  }
  showCreateForm() {
    const {setCreateFormState} = this.props
    setCreateFormState(true);
  }
  render() {
    return (
      <div className={styles.AdditionBtn} onClick={this.showCreateForm}>
        <i className="fas fa-plus" style={styles.icon}></i>
        <p className={styles.hint}>Add task</p>
      </div>
    )
  }
}



