import React, { Component } from 'react'
import PropTypes from 'prop-types';
import styles from './_ListContent.scss';
import ListButton from '../ListButton'

export default class ListContent extends Component {
  constructor(props) {
    super(props)
    this.state = {
      okBtn: this.props.createMode ? 'Add Task' : 'Save',
      isShow: this.props.showContent,
      showContent: this.props.showContent ? styles.show : ''
    }
    this.cancel = this.cancel.bind(this);
    this.addTask = this.addTask.bind(this);
    this.toggleContent = this.toggleContent.bind(this);
  }
  cancel(){
    this.props.onCancel();
  }
  addTask(){
    this.props.onAddTask();
  }
  toggleContent({showContent}){
    this.setState({
      isShow: showContent,
      showContent: showContent ? styles.show : ''
    })
  }
  componentDidMount() {
    this.props.setClick(this.toggleContent);
  }
  render() {
    return (
      <div className={`${styles.ListContent} ${this.state.showContent}`}>
        <div className={styles.container}>
          <div className={`${styles.area}`}>
            <div className={styles.title}>
              <i className="far fa-calendar-altfar fa-calendar-alt" />         
              <span>Deadline</span>
            </div>
            <div className={`${styles.content} ${styles.calender}`}>
              <div className={`${styles.date1}`}>
                <input id='date' type='date' defaultValue={this.props.item.date}/> 
              </div>
              <div className='date2'>
                <input id="time" type="time" />
              </div>
            </div>
          </div>
          <div className={`${styles.area}`}>
            <div className={styles.title}>
              <i className="far fa-file"></i>
              <span>File</span>
            </div>
            <div className={`${styles.content} ${styles.file}`}>
              <i className={`fas fa-plus-square ${styles['fa-plus-square']}`}></i>
            </div>
          </div>
          <div className={`${styles.area}`}>
            <div className={styles.title}>
              <i className="far fa-comment-dots"></i>
              <span>Comment</span>
            </div>
            <div>
              <div className={`${styles.content} ${styles.comment}`}>
                <textarea placeholder="Type your memo here..." defaultValue={this.props.item.comment}></textarea>
              </div>
            </div>
          </div>
        </div>
        <div className={`${styles.btnGroup}`}>
          <ListButton icon="fa-times" text="Cancel" onClick={this.cancel}/>
          <ListButton icon="fa-plus" text={this.state.okBtn} onClick={this.addTask}/>
        </div>
      </div>
    )
  }
}

ListContent.propTypes = {
  showContent: PropTypes.bool,
  item: PropTypes.object
}

ListContent.defaultProps = {
  showContent: false,
  item: {},
}


